import { Core, Fetch } from "@true/core";
import { batch } from "react-redux";
import { ICoreValue, ICreateCoreValue } from "@/store/corevalues/type";
import { setCoreValuesQuery, syncCoreValues } from "@/store/corevalues/index";

export const requestCoreValues = async () => {
  const { data } = await Fetch.postWithToken<{
    core_values: ICoreValue[];
  }>(`@${Core.systemPath}/inside/ajax/api/company/corevalue/gets`);

  batch(() => {
    if (data["core_values"].length) {
      syncCoreValues(data.core_values);
      setCoreValuesQuery(prev => ({
        ...prev,
        ["all"]: data["core_values"].map(item => item.id.toString())
      }));
    }
  });
  return data["core_values"];
};
export const requestCoreValueId = async (params: {
  corevalue_id: string | number;
}) => {
  const { data } = await Fetch.postWithToken<{
    cv: ICoreValue;
  }>(
    `@${Core.systemPath}/inside/ajax/api/company/corevalue/get?corevalue_id=${params.corevalue_id}`
  );

  batch(() => {
    if (data.cv) {
      syncCoreValues([data.cv]);
    }
  });
  return data.cv;
};
