import {memo, useCallback, useEffect} from 'react';
import {ActivityIndicator} from 'react-native';
import {styled, useAsyncFn, useMe} from '@true/core';
import {FlashList} from '@shopify/flash-list';
import {ScreenWrapper} from '@true/ui';
import {requestCoreValues} from '@/store/corevalues/function';
import {CoreValueThumbnail} from '@/screens/CoreValueScreen/CoreValueThumbnail';
import {useCoreValuesByQuery} from '@/store/corevalues';
import {CustomHeader} from '@/components/CustomHeader';
import {navigateToCreateCoreValueScreen} from '@/utils';
import {EUserRole} from '@/utils/helper';

export const CoreValueScreen = memo(function () {
  const listCoreValues = useCoreValuesByQuery('all');
  const me = useMe();
  const renderItem = useCallback(({item, index}: any) => {
    return <CoreValueThumbnail id={item} index={index + 1} />;
  }, []);

  const [{loading}, refreshLetter] = useAsyncFn(async () => {
    await requestCoreValues();
    return;
  }, []);

  const onCreate = useCallback(() => {
    if (me && Number(me?.role) === EUserRole.ADMIN) {
      navigateToCreateCoreValueScreen({
        id: -1,
      });
    }
    return null;
  }, [me]);

  useEffect(() => {
    requestCoreValues().then();
  }, []);

  return (
    <ScreenWrapper>
      <CustomHeader
        isBack
        title={'Board letters'}
        onCreate={onCreate}
        isSearch={false}
        isDivider
      />
      <SContainer>
        <SFlashList
          data={listCoreValues}
          renderItem={renderItem}
          numColumns={2}
          keyExtractor={keyExtractor}
          refreshing={loading}
          onRefresh={refreshLetter}
          onEndReachedThreshold={1}
          removeClippedSubviews={true}
          // ListHeaderComponent={renderHeader}
          ListEmptyComponent={<ActivityIndicator />}
        />
      </SContainer>
    </ScreenWrapper>
  );
});

const SFlashList = styled(FlashList)`
  flex: 1;
`;
const keyExtractor = (item: any) => item;
const SContainer = styled.View`
  flex: 1;
  background-color: white;
  padding-left: 20px;
`;
export default CoreValueScreen;
